import React from "react";

import CountryFlag from "shared/components/countryFlag";
import { useClocksData } from "shared/hooks/clocksData";

import "./styles.scss";

export default function Clocks({ otherTimeZones }: T_ClocksProps) {
  const clocksData = useClocksData(otherTimeZones);

  return (
    <div className="Clocks">
      {clocksData.map((clock) => (
        <div key={clock.timeZoneName} className="clockWrap">
          <div className="clockMeta">
            <div className="clockMetaHeadWrap">
              <CountryFlag code={clock.countryCode} />
              &nbsp;
              <span className="clockMetaTitle">{clock.countryName}</span>
              &nbsp;
              {clock.isLocal && (
                <svg
                  className="-mt-1 w-5 h-5"
                  fill="#bbb"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M10.707 2.293a1 1 0 00-1.414 0l-7 7a1 1 0 001.414 1.414L4 10.414V17a1 1 0 001 1h2a1 1 0 001-1v-2a1 1 0 011-1h2a1 1 0 011 1v2a1 1 0 001 1h2a1 1 0 001-1v-6.586l.293.293a1 1 0 001.414-1.414l-7-7z"></path>
                </svg>
              )}
            </div>
            {clock.weekdayLong},<br /> {clock.day} {clock.monthLong}{" "}
            {clock.year}
          </div>
          <div className="analog-clock">
            <div className="analog-clock-face">
              <div className="dots"></div>
              <div className="dots"></div>
              <div className="dots"></div>
              <div className="dots"></div>
              <div className="dots"></div>
              <div className="dots"></div>
              <div
                style={{ transform: `rotate(${clock.hourDeg}deg)` }}
                className="hand hour-hand"
              ></div>
              <div
                style={{ transform: `rotate(${clock.minuteDeg}deg)` }}
                className="hand min-hand"
              ></div>
              <div
                style={{ transform: `rotate(${clock.secondDeg}deg)` }}
                className="hand sec-hand"
              ></div>
              {clock.isInDST && (
                <i className="pt-2 text-center block tracking-wider text-gray-400 text-xs">
                  daylight
                  <br />
                  saving
                </i>
              )}
            </div>
          </div>
          <div className="digital-clock">
            <div className="digit digit-hour">{clock.hour}</div>
            <span className="self-center text-gray-300 animate-ping">:</span>
            <div className="digit digit-min">{clock.minute}</div>
            <div className="digit digit-sec">{clock.second}</div>
            <div className="meridiem">{clock.meridiem}</div>
          </div>
        </div>
      ))}
      <div className="clockWrap !justify-center transition duration-300 ease-in-out transform hover:-translate-y-1 hover:shadow-lg group cursor-pointer">
        <span className="self-center group-hover:scale-125 text-2xl">âž•</span>
      </div>
    </div>
  );
}

type T_ClocksProps = {
  otherTimeZones?: string[];
};
