import React from "react";

import "./styles.scss";

export default function CountryFlag({
  code,
  iconClassName = "",
  wrapperClassName = ""
}: T_CountryFlagProp) {
  return (
    <span className={wrapperClassName}>
      <i
        className={`countryFlagSprite countryFlag-${code.toLowerCase()} ${iconClassName}`}
      ></i>
    </span>
  );
}

export type T_CountryFlagProp = {
  code: string;
  wrapperClassName?: string;
  iconClassName?: string;
};
